.footer {
	width: 100%;
	position: fixed;
	bottom: 0;
	left: 0;
	display: flex;
	align-items: center;
	justify-content: center;
	background-color: #f2f3f77d;
	padding: 40px;
}

.footer_links {
	display: flex;
	align-items: center;
	gap: 120px;
}

.footer_links__item {
	color: #000;
	font-size: 18px;
	font-style: normal;
	font-weight: 400;
	line-height: normal;
}
